.projects {
  display: flex;
  flex-direction: column;
  height: auto;
  width: 100%;
  flex-wrap: wrap;
  overflow-x: hidden; }
  .projects .project {
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center center; }
    .projects .project > div {
      display: flex;
      flex-direction: row;
      background-color: rgba(39, 39, 39, 0.8); }
      .projects .project > div .screenshots {
        width: 23em;
        height: 100vh;
        overflow: hidden;
        margin-left: auto;
        margin-right: 4em;
        margin-top: 0; }
        .projects .project > div .screenshots > div {
          margin-top: 0;
          position: relative;
          top: 0;
          width: 100%; }
          .projects .project > div .screenshots > div .screenshot {
            width: 100%;
            height: 12em;
            background-size: cover;
            background-position: center center;
            cursor: pointer; }
      .projects .project > div .info {
        padding: 4em;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-end;
        width: calc(100vw - 23em);
        box-sizing: border-box; }
        .projects .project > div .info > div {
          display: flex;
          align-items: center;
          margin-bottom: 1em; }
          .projects .project > div .info > div h2 {
            font-weight: 200;
            font-size: 2.4em;
            letter-spacing: 1px; }
          .projects .project > div .info > div .project_link {
            display: inline-block;
            width: 2.3em;
            height: 2.3em;
            margin-left: 0.5em;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center; }
          .projects .project > div .info > div .handle {
            margin-right: 0.5em; }
        .projects .project > div .info .link {
          color: #ff69b4;
          text-decoration: none;
          border-bottom: 1px solid transparent;
          border-top: 1px solid transparent; }
          .projects .project > div .info .link:hover {
            border-bottom: 1px solid #ff69b4; }
        .projects .project > div .info p {
          font-size: 1.5em;
          color: rgba(255, 255, 255, 0.7);
          letter-spacing: 0.5px; }
